import { Preflight } from '@common/js/Preflight.js'
import { Main, MainBorder } from '@common/js/Build.js'
import { Animation } from '@common/js/Animation.js'
import { Core, ImageManager } from 'ad-control'
import { Control } from '@common/js/Control.js'
import { Styles, Markup, Align, Effects, Clamp } from 'ad-view'

import '@size/images/intro_logo.png'
import '@size/images/intro_logo_wordmark.png'

export class Ad {
  // called from index.html onImpression()
  static launch(binaryAssets) {
    console.log('Ad.launch()')
    Core.init(binaryAssets)
      .then(() => Preflight.init())
      .then(() => Core.loadDynamic())
      .then(() => Ad.prepare())
      .catch(err => {
        throw err
      })
  }

  static prepare() {
    console.log('Ad.prepare()')
    Control.preMarkup()
    adData.preloaderScaleTo = 35
    let preloaderScale = 0.5 * 0.35
    let introLogoData = ImageManager.get('intro_logo')
    let introWordmarkData = ImageManager.get('intro_logo_wordmark')
    View.main = new Main({
      endframe: {
        css: {
          clip: `rect(0px, 170px, ${adParams.adHeight}px, 155px)`
        },
        header: {
          css: {
            width: 242
            // height: 20
          },
          fontSize: 17,
          align: {
            x: {
              type: Align.CENTER,
              against: 178
            },
            y: Align.CENTER
          }
        },
        sub: {
          css: {
            width: 242
            // height: 20
          },
          fontSize: 17,
          align: {
            x: {
              type: Align.CENTER,
              against: 178
            },
            y: Align.CENTER
          }
        },
        logo: {
          align: {
            x: {
              type: Align.LEFT,
              offset: 10
            },
            y: Align.CENTER
          }
        },
        cta: {
          css: {
            width: 68,
            height: 17
          },
          txt: {
            fontSize: 10,
            align: {
              y: {
                type: Align.CENTER,
                offset: 1
              }
            }
          }
        }
      },
      introLogo: {
        css: {
          width: introLogoData.width * preloaderScale,
          height: introLogoData.height * preloaderScale,
          transformOrigin: `${introLogoData.width * 0.5 * preloaderScale}px ${introLogoData.height * 0.35 * preloaderScale}px`
        },
        retina: false
      },
      introWordmark: {
        css: {
          width: introWordmarkData.width * preloaderScale,
          height: introWordmarkData.height * preloaderScale
        },
        retina: false
      }
    })
    View.mainBorder = new MainBorder()
    Control.postMarkup()
    Animation.start()
  }
}
window.Ad = Ad
