import { UIComponent, UIBorder, UIButton, UIImage, TextFormat, UITextField, UIGroup } from 'ad-ui'
import { Styles, Markup, Align, Effects, Clamp } from 'ad-view'
import { ObjectUtils } from 'ad-utils'
import { Gesture, GestureEvent } from 'ad-events'

import '@size/images/frame_T.png'
import '@size/images/frame_B.png'
import '@size/images/frame_L.png'
import '@size/images/frame_R.png'
import '@size/images/frame_quibiLogo.png'

export default class Frame {
  constructor(args) {
    // super(ObjectUtils.defaults(args, {}, true))

    var T = new UIComponent({
      target: args.target,
      id: 'frameContainer',
      css: {
        width: 'inherit',
        height: 'inherit'
      }
    })

    T.overlayOpacity = 0.45

    T.heroOverlay = new UIComponent({
      target: T,
      id: 'heroOverlay'
    })

    T.hero = new UIImage({
      target: T,
      id: 'frame_hero',
      source: adData.images[0].hero,
      css: {
        backgroundPosition: 'right',
        backgroundSize: 'auto'
      },
      align: {
        y: Align.BOTTOM
      }
    })

    T.left = new UIImage({
      target: T,
      id: 'frame_left',
      source: 'frame_L'
    })

    T.top = new UIImage({
      target: T,
      id: 'frame_top',
      source: 'frame_T',
      align: {
        x: {
          type: Align.RIGHT,
          against: T.left,
          outer: true
        }
      }
    })

    T.right = new UIImage({
      target: T,
      id: 'frame_right',
      source: 'frame_R',
      align: {
        x: {
          type: Align.RIGHT,
          against: T.top,
          outer: true
        }
      }
    })

    T.bottom = new UIImage({
      target: T,
      id: 'frame_bottom',
      source: 'frame_B',
      align: {
        x: {
          type: Align.RIGHT,
          against: T.left,
          outer: true
        },
        y: {
          type: Align.BOTTOM,
          against: T.left
        }
      }
    })

    T.frame = new UIGroup({
      target: T,
      id: 'frame_group',
      children: [T.left, T.top, T.right, T.bottom],
      align: ObjectUtils.defaults(args.align, { x: Align.CENTER, y: Align.CENTER }, true)
    })

    TweenLite.set(T.hero, {
      width: T.frame.width,
      height: T.frame.height,
      x: T.frame.x,
      y: T.frame.y,
      backgroundPosition: `${-T.frame.x}px ${-(T.frame.y - T.hero.y)}px`,
      borderRadius: args.borderRadius,
      overflow: 'hidden'
    })

    TweenLite.set(T.heroOverlay, {
      width: T.frame.width,
      height: T.frame.height,
      x: T.frame.x,
      y: T.frame.y,
      borderRadius: args.borderRadius,
      backgroundColor: adData.colors.black,
      opacity: T.overlayOpacity * 0.5
    })
    T.heroOverlay.scaleTo = T.frame.width / T.frame.height > 1 ? (T.height * 1.25) / T.frame.height : (T.width * 1.25) / T.frame.width

    T.hero.hide()
    T.heroOverlay.hide()

    // Clamp.set(T, Clamp.XY)
    // Align.set(T, ObjectUtils.defaults(args.align, { x: Align.CENTER, y: Align.CENTER }, true))

    T.logo = new UIImage(
      ObjectUtils.defaults(
        args.logo,
        {
          target: T.frame,
          id: 'frame_quibi_logo',
          source: 'frame_quibiLogo',
          retina: true
        },
        true
      )
    )

    /* T.copy = new UIComponent(
			ObjectUtils.defaults(
				args.copy,
				{
					target: T,
					id: 'copy'
				},
				true
			)
		) */

    T.header = new UITextField(
      ObjectUtils.defaults(
        args.copy.header,
        {
          target: T.frame,
          // target: T.copy,
          id: 'header',
          css: {
            color: adData.colors.white,
            textShadow: `${adData.colors.black} 0px 0px 5px`
          },
          fontSize: 18,
          fontFamily: adData.fonts.black,
          format: TextFormat.INLINE_FIT_CLAMP,
          textAlign: Align.CENTER,
          text: adData.copy.header
        },
        true
      )
    )

    T.sub = new UITextField(
      ObjectUtils.defaults(
        args.copy.sub,
        {
          target: T.frame,
          // target: T.copy,
          id: 'sub',
          css: {
            color: adData.colors.white,
            textShadow: `${adData.colors.black} 0px 0px 5px`
          },
          fontSize: 13,
          fontFamily: adData.fonts.regular,
          format: TextFormat.INLINE_FIT_CLAMP,
          textAlign: Align.CENTER,
          // spacing: -0.5,
          text: adData.copy.sub,
          align: {
            x: {
              type: Align.CENTER,
              against: T.header
            },
            y: {
              type: Align.BOTTOM,
              against: T.header,
              outer: true
            }
          }
        },
        true
      )
    )

    T.copy = new UIGroup(
      ObjectUtils.defaults(
        args.copy,
        {
          target: T.frame,
          id: 'copy',
          children: [T.header, T.sub],
          align: {
            x: Align.CENTER
          }
        },
        true
      )
    )

    Gesture.disable(T)

    T.animateIN = function(delay) {
      let duration = 1

      TweenLite.from(T.logo, duration * 0.35, { delay: delay, scale: 0, ease: Power2.easeIn })
      delay += duration * 0.35

      TweenLite.fromTo(
        T.right,
        duration / 4,
        { clip: `rect(${T.right.height}px, ${T.right.width}px, ${T.right.height}px, 0px)` },
        { delay: delay, clip: `rect(0px, ${T.right.width}px, ${T.right.height}px, 0px)`, ease: Linear.easeNone }
      )
      delay += duration / 4
      TweenLite.fromTo(
        T.top,
        duration / 4,
        { clip: `0px, ${T.top.width}px, ${T.top.height}px, ${T.top.width}px)` },
        { delay: delay, clip: `rect(0px, ${T.top.width}px, ${T.top.height}px, 0px)`, ease: Linear.easeNone }
      )
      TweenLite.from(T.header, duration * 0.5, { delay: delay, opacity: 0, y: '-=10', ease: Power2.easeOut })
      TweenLite.from(T.sub, duration * 0.5, { delay: delay + 0.2, opacity: 0, y: '-=10', ease: Power2.easeOut })
      delay += duration / 4
      TweenLite.fromTo(T.left, duration / 3 / 2, { clip: `0px, ${T.left.width}px, 0px, 0px)` }, { delay: delay, clip: `rect(0px, ${T.left.width}px, ${T.left.height}px, 0px)`, ease: Linear.easeNone })
      delay += duration / 4
      TweenLite.fromTo(
        T.bottom,
        duration / 2,
        { clip: `0px, 0px, ${T.bottom.height}px, 0px)` },
        { delay: delay, clip: `rect(0px, ${T.bottom.width}px, ${T.bottom.height}px, 0px)`, ease: Power2.easeOut }
      )
      delay += duration / 3
      return delay
    }

    T.onOver = function() {
      T.hero.show()
      T.heroOverlay.show()
      TweenLite.to(T.heroOverlay, adData.animation.duration * 0.5, { scale: T.heroOverlay.scaleTo, opacity: T.overlayOpacity, ease: Power2.easeOut })
    }

    T.onOut = function() {
      TweenLite.to(T.heroOverlay, adData.animation.duration * 0.5, { scale: 1, opacity: T.overlayOpacity * 0.5, ease: Power2.easeInOut })
    }

    return T
  }
}
