import { UIComponent, UIBorder, UIButton, UIImage, TextFormat, UITextField, UIGroup } from 'ad-ui'
import { Styles, Markup, Align, Effects, Clamp } from 'ad-view'
import { ObjectUtils } from 'ad-utils'

class CtaBtn extends UIButton {
  constructor(args) {
    super(
      ObjectUtils.defaults(
        args,
        {
          id: 'cta',
          css: {
            width: 148,
            height: 29,
            borderRadius: 14,
            overflow: 'hidden'
            // backgroundColor: adData.colors.white
          }
        },
        true
      )
    )

    var T = this

    T.out = new UIComponent({
      target: T,
      id: 'cta_out',
      css: {
        width: 'inherit',
        height: 'inherit',
        backgroundColor: adData.colors.white
      }
    })

    T.txt = new UITextField(
      ObjectUtils.defaults(
        args.txt,
        {
          target: T.out,
          id: 'cta_out_txt',
          css: {
            width: 'inherit',
            height: 'inherit',
            color: adData.colors.black
          },
          fontSize: 18,
          fontFamily: adData.fonts.bold,
          format: TextFormat.INLINE_FIT,
          alignText: Align.CENTER,
          text: adData.copy.cta
        },
        true
      )
    )

    T.over = new UIComponent({
      target: T,
      id: 'cta_over',
      css: {
        width: 'inherit',
        height: 'inherit',
        background: `linear-gradient(133deg, #a62cf4 0%, #7725ff 100%)`
      }
    })

    T.txt = new UITextField(
      ObjectUtils.defaults(
        args.txt,
        {
          target: T.over,
          id: 'cta_over_txt',
          css: {
            width: 'inherit',
            height: 'inherit',
            color: adData.colors.white
          },
          fontSize: 18,
          fontFamily: adData.fonts.bold,
          format: TextFormat.INLINE_FIT,
          alignText: Align.CENTER,
          text: adData.copy.cta
        },
        true
      )
    )

    // TweenLite.set(T.over, { clip: `rect( 0px, ${T.over.width}px, 0px, 0px)`, opacity: 0 })
    TweenLite.set(T.over, { opacity: 0 })

    T.animateIN = function(delay) {
      TweenLite.from(T, adData.animation.duration, { delay: delay, y: '-=10', opacity: 0, ease: Power2.easeOut })
      delay += adData.animation.duration
      return delay
    }

    T.onOver = function() {
      // TweenLite.to(T.over, adData.animation.duration * 0.5, { clip: `rect( 0px, ${T.over.width}px, ${T.over.height}px, 0px)`, opacity: 1, ease: Power4.easeOut })
      TweenLite.to(T.over, adData.animation.duration * 0.5, { opacity: 1, ease: Power4.easeOut })
      TweenLite.to(T.out, adData.animation.duration * 0.5, { opacity: 0, ease: Power2.easeInOut })
    }

    T.onOut = function() {
      /* TweenLite.to(T.over, adData.animation.duration * 0.5, {
        clip: `rect( 0px, ${T.over.width}px, 0px, 0px)`,
        opacity: 0,
        ease: Power2.easeInOut
      }) */
      TweenLite.to(T.over, adData.animation.duration * 0.5, {
        opacity: 0,
        ease: Power2.easeInOut
      })
      TweenLite.to(T.out, adData.animation.duration * 0.5, { opacity: 1, ease: Power2.easeOut })
    }

    return T
  }
}

export default CtaBtn
