import { Gesture, GestureEvent } from 'ad-events'

export class Control {
  static preMarkup() {
    console.log('Control.preMarkup()')
  }

  static postMarkup() {
    console.log('Control.postMarkup()')
    // listen for default exit
    Gesture.add(View.main, GestureEvent.CLICK, Control.handleClick)
    Gesture.add(View.main, GestureEvent.OVER, function() {
      adData.rolledOver = true
    })
    Gesture.add(View.main, GestureEvent.OUT, function() {
      adData.rolledOver = false
    })
  }

  static enableRollovers() {
    Gesture.add(View.main, GestureEvent.OVER, Control.handleOver)
    Gesture.add(View.main, GestureEvent.OUT, Control.handleOut)
    if (adData.rolledOver) Control.handleOver()
  }

  static handleClick(event) {
    Network.exit(clickTag)
  }

  static handleOver(event) {
    if (View.main.endframe.frame) View.main.endframe.frame.onOver()
    if (View.main.endframe.cta) View.main.endframe.cta.onOver()
  }

  static handleOut(event) {
    if (View.main.endframe.frame) View.main.endframe.frame.onOut()
    if (View.main.endframe.cta) View.main.endframe.cta.onOut()
  }
}
